          >
            <summary-statistics :data="data.statisticsItems" />
          </b-col>
        </b-row>

        <b-row class="match-height">
          <b-col lg="4">
            <b-row class="match-height">
              <b-col
                lg="12"
                md="6"
              >
                <summary-earnings-chart :data="data.earningsChart" />
              </b-col>
            </b-row>
          </b-col>

          <!-- Revenue Report Card -->
          <b-col lg="8">
            <summary-revenue-report :data="data.revenue" />
          </b-col>
          <!--/ Revenue Report Card -->
        </b-row>
      </b-tab>
      <b-tab lazy>
        <template #title>
          <feather-icon icon="DropletIcon"   stroke="darkcyan" fill="cyan"/>
          <span>Account - 00012864</span>
        </template>
        <b-row class="match-height">
          <b-col
            xl="4"
            md="6"
          >
            <water-summary-medal :data="dataWater.congratulations" />
          </b-col>
          <b-col
            xl="8"
            md="6"
          >
            <water-summary-statistics :data="dataWater.statisticsItems" />
          </b-col>
        </b-row>

        <b-row class="match-height">
          <b-col lg="4">
            <b-row class="match-height">
              <b-col
                lg="12"
                md="6"
              >
                <water-summary-earnings-chart :data="dataWater.earningsChart" />
              </b-col>
            </b-row>
          </b-col>

          <!-- Revenue Report Card -->
          <b-col lg="8">
            <water-summary-revenue-report :data="dataWater.revenue" />
          </b-col>
