</template>

<script>
import {
  BRow, BCol, BTab, BTabs,
} from 'bootstrap-vue'

import { getUserData } from '@/auth/utils'
import SummaryMedal from './electric/SummaryMedal.vue'
import SummaryStatistics from './electric/SummaryStatistics.vue'
import SummaryRevenueReport from './electric/SummaryRevenueReport.vue'
import SummaryEarningsChart from './electric/SummaryEarningsChart.vue'

import WaterSummaryMedal from './water/WaterSummaryMedal.vue'
import WaterSummaryStatistics from './water/WaterSummaryStatistics.vue'
import WaterSummaryRevenueReport from './water/WaterSummaryRevenueReport.vue'
import WaterSummaryEarningsChart from './water/WaterSummaryEarningsChart.vue'
import BlogList from '../../pages/blog/BlogList.vue'

export default {
  components: {
    BRow,
    BCol,
    BTab,
    BTabs,
    WaterSummaryMedal,
    WaterSummaryStatistics,
    WaterSummaryRevenueReport,
    WaterSummaryEarningsChart,
    SummaryMedal,
    SummaryStatistics,
    SummaryRevenueReport,
    SummaryEarningsChart,
    BlogList,
  },
  data() {
    return {
      data: {},
      dataWater: {},
    }
  },
  created() {
    this.$http.get('/summary/data')
      .then(response => {
        this.data = response.data

        const userData = getUserData()
        this.data.congratulations.name = userData.fullName.split(' ')[0] || userData.username
      })
    this.$http.get('/summaryWater/data')
      .then(response => {
        this.dataWater = response.data

        const userData = getUserData()
        this.dataWater.congratulations.name = userData.fullName.split(' ')[0] || userData.username
      })
  },
}
</script>

